.header {
    display: flex;
    align-items: center;
    height: 2rem;
    background: rgba(19,19,19,255)
}

.header div {
    display: flex;
    align-items: center;
    margin: 0 auto;
}

.header span, a{
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
}

.header a ,span:hover{
    cursor: pointer;
}

.header * {
    margin: 0.5rem;
    font-size: 0.8rem;
    color: rgba(123,123,123,255)
}





.container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 60px;
    padding-bottom: 100px;
    outline: none;
    
  }

.sign-frame {
    height: 300px;
    width: 250px;
    background:
      linear-gradient(
      rgba(35,43,85,0.75),
      rgba(35,43,85,0.95));
      background-size: cover;
  margin-left: auto;
  margin-right: auto;
  border-top: solid 1px rgba(255,255,255,.5);
  border-radius: 5px;
  box-shadow: 0px 2px 7px rgba(0,0,0,0.2);
  overflow: hidden;
  transition: all .5s ease;
}

.sign-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px;
}

.login-btn {
  cursor: pointer;
  color: white;
  border: 3px solid white;
  border-radius: 10px;
  background: transparent;
  padding: 3px 10px 3px 10px;
}

.login-btn:hover {
  background: rgba(57, 63, 90, 0.75);
}

.login-btn:active {
  background: rgba(12, 26, 87, 0.75);
}

.close-btn {
  cursor: pointer
}

.btn-signin {
  color: #efefef;
  cursor: pointer;
}
.sign-input {
  background: transparent;
  border: none;
  outline: none;
  border-bottom: solid 1px #efefef;
  margin: 4px;
  color: #efefef;
}

.form-signup {
    text-align: center;
    font-size: small;
}

.form-signup input{
    width: 50%;
}



